<template>
  <div>
    <!-- <headerBar :header-data="headerData" /> -->
    <!-- header begin -->
    <div class="nav-bar">
      <div class="nav-bar-left" @click="goRzIndex()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">浏览工作日志</div>
      <div class="nav-bar-right" @click="showSearchPopup()">
        <i class="iconfont iconsousuo" />
      </div>
    </div>
    <!-- header end -->

    <!-- 查询条件  begin -->
    <van-popup v-model="SeachShow" position="top" class="search-popup" overlay-class="transparent-overlay">
      <div class="common-main box">
        <div class="spe-input">
          <van-cell title="选择人员" :value="renyuan" is-link @click="RenYuanshow = true" />
          <van-cell title="选择开始日期" :value="date" is-link @click="show = true" />
          <van-cell title="选择截止日期" :value="date1" is-link @click="show1 = true" />
          <van-calendar v-model="show" :min-date="minDate" :max-date="maxDate" color="#1890ff" @confirm="onConfirm" />
          <van-calendar v-model="show1" :min-date="minDate1" color="#1890ff" @confirm="onConfirm1" />
        </div>
        <div class="search-popup-btn">
          <van-button type="info" round size="large" plain @click="reset()">重置</van-button>
          <van-button type="info" round size="large" @click="getRzList()">搜索</van-button>
        </div>
      </div>
    </van-popup>
    <!-- 查询条件  end -->

    <div class="container">
      <div v-if="rzList && rzList.length > 0" class="content">
        <div class="common-main box">
          <div class="page-list">
            <div class="pagelist-left">当前人员</div>
            <div class="pagelist-right">{{ renyuan }}</div>
          </div>
        </div>
      </div>
      <!-- 操作提示 begin -->
      <div v-if="firstShow" class="rizhi-guide" />
      <!-- 操作提示 end -->

      <van-collapse v-model="activeNames">
        <!-- 循环列表 begin -->
        <common-list
          ref="dida_list"
          class="common-list"
          :post-url="list.postUrl"
          :req-data="list.reqData"
          :data-name="list.dataName"
          :apptype-show="false"
          @returnData="getPrizeLog($event)"
        >
          <div v-for="(item,index) of rzList" :key="index" class="content">
            <div class="common-main box">
              <van-collapse-item :name="index+1">
                <template #title>
                  <div class="work-rizhi-date">
                    <h4>{{ formatDate12(item.rzsj) }} {{ getWeek(item.rzsj) }}</h4>
                  </div>
                </template>
                <div class="work-rizhi">
                  <div v-for="(item1,index1) of item.rzmxArr" :key="index1" class="work-list">
                    <div class="work-xm">
                      <span>{{ getShow(item1) }}</span><em>{{ item1.gzl }}/{{ item1.sdgzl }}</em>
                    </div>
                    <div class="work-content">
                      <p>
                        {{ item1.gznr }}
                      </p>
                    </div>
                  </div>
                </div>
                <div class="page-list audit-opinion">
                  <div class="pagelist-left">审核意见</div>
                  <div class="pagelist-right">{{ item.shrmc }}：{{ item.spyj }}</div>
                </div>
              </van-collapse-item>
            </div>
          </div>
        <!-- 循环列表 end -->
        </common-list>
      </van-collapse>

    </div>

    <!-- 选择部门-人员弹窗 begin -->
    <van-popup v-model="RenYuanshow" round position="bottom">
      <van-cascader
        v-model="cascaderValue"
        class="cascader-flex"
        title="选择人员"
        :options="options"
        :prop="defaultParams"
        @close="RenYuanshow = false"
        @change="onChange"
        @finish="onFinish"
      />
    </van-popup>
    <!-- 选择部门-人员弹窗 end -->
    <div class="common-space" />
    <Backtop />
  </div>
</template>

<script land="script" scoped>
// import headerBar from '@/components/header/headerBar.vue'
import Backtop from '@/components/backtop/backtop' // 返回顶部
import commonList from '@/components/list/commonList1.vue'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'LlGzrz',
  components: {
    // headerBar,
    Backtop,
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      shztValue: [1, 2, 3],
      date1: '请选择日期',
      show1: false,
      SeachShow: false,
      maxDate: new Date(2100, 0, 1),
      minDate: new Date(1999, 0, 1),
      minDate1: new Date(1999, 0, 1),
      // headerData: {
      //   title: '浏览工作日志',
      //   leftUrl: 'back',
      //   leftText: ' '
      // },
      defaultParams: {
        label: 'rymc',
        value: 'ryid'
      },
      date: '请选择日期',
      renyuan: '请选择人员',
      show: false,
      rymc: '',
      RenYuanshow: false,
      activeNames: ['1'],
      options: [],
      cascaderValue: '',
      rzList: [],
      ksrq: '',
      jsrq: '',
      firstShow: true
    }
  },
  created() {
    this.getBmList()
    this.getGzrzLists()
  },
  methods: {
    getShow(rzxx) {
      if (rzxx.xmmc === '请选择项目' || !rzxx.xmmc) {
        return rzxx.gzxzmc
      } else {
        return rzxx.xmmc
      }
    },
    reset() {
      this.renyuan = '请选择人员'
      this.date = '请选择日期'
      this.date1 = '请选择日期'
      this.ryid = ''
      this.ksrq = ''
      this.jzrq = ''
    },
    getPrizeLog(data) {
      this.firstShow = false
      this.rzList = data
    },
    getGzrzLists() {
      this.list.postUrl = 'erp/getGzrzAction'
      this.list.reqData = {
        ksrq: this.ksrq,
        jzrq: this.jsrq,
        ryid: this.rymc,
        shbz: this.shztValue
      }
      this.list.dataName = 'gzrzArr'
    },
    onChange(event) {
      if (event.tabIndex === 1) {
        return
      }
      this.$store.dispatch('erp/getRyByBmAction', {
        bmid: event.value
      }).then(response => {
        const arr = []
        for (let j = 0; j < response.length; j++) {
          arr.push({
            text: response[j].rymc,
            value: response[j].ryid
          })
        }
        for (let i = 0; i < this.options.length; i++) {
          if (this.options[i].value === event.selectedOptions[0].value) {
            this.options[i].children = arr
          }
        }
      })
    },
    getWeek(date) {
      date = date + ''
      const str = date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
      const temp = new Date(str)
      if (temp.getDay() === 1) {
        return '周一'
      } else if (temp.getDay() === 2) {
        return '周二'
      } else if (temp.getDay() === 3) {
        return '周三'
      } else if (temp.getDay() === 4) {
        return '周四'
      } else if (temp.getDay() === 5) {
        return '周五'
      } else if (temp.getDay() === 6) {
        return '周六'
      } else if (temp.getDay() === 0) {
        return '周日'
      }
    },
    getRzList() {
      this.SeachShow = false
      const reqData = {
        ksrq: this.ksrq,
        jzrq: this.jsrq,
        ryid: this.rymc,
        shbz: this.shztValue
      }
      this.$refs.dida_list.onRefresh(reqData)
      // this.$store.dispatch('erp/getGzrzAction', {
      //   ksrq: this.ksrq,
      //   jzrq: this.jsrq,
      //   ryid: this.rymc,
      //   shbz: [1, 2]
      // }).then(res => {
      //   this.rzList = res
      // }).catch(error => {
      //   this.$toast(error)
      // })
    },
    getBmList() {
      this.$store.dispatch('erp/getBmxxAction').then(res => {
        for (let i = 0; i < res.length; i++) {
          this.options.push({
            text: res[i].bmmc,
            value: res[i].bmbh,
            children: []
          })
          // this.$store.dispatch('erp/getRyByBmAction', {
          //   bmid: res[i].bmbh
          // }).then(response => {
          //   const arr = []
          //   for (let j = 0; j < response.length; j++) {
          //     arr.push({
          //       text: response[j].rymc,
          //       value: response[j].ryid
          //     })
          //   }
          //   this.options[i].children = arr
          // })
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    onConfirm(date) {
      this.ksrq = this.formatDate1(date)
      this.date = this.formatDate(date)
      this.minDate1 = date
      this.show = false
    },
    onConfirm1(date) {
      this.jsrq = this.formatDate1(date)
      this.date1 = this.formatDate(date)
      this.maxDate = date
      this.show1 = false
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if ((date.getMonth() + 1) < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + (date.getDate())
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    formatDate12(date) {
      date = date + ''
      return date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
    },
    // 全部选项选择完毕后，会触发 finish 事件
    onFinish({ selectedOptions }) {
      this.rymc = selectedOptions[1].value
      this.RenYuanshow = false
      this.renyuan = selectedOptions.map((option) => option.text).join('/')
      console.log(' this.rymc====================================', selectedOptions)
    },
    goRzIndex() {
      this.$router.push({
        name: 'RzIndex'
      })
    },
    showSearchPopup() {
      this.SeachShow = true
    }
  }
}
</script>

<style scoped>
.common-main{padding: 5px 10px;}
.van-collapse-item--border::after{display: none;}
</style>
